import React,{Component} from 'react'
import { hashHistory } from 'react-router'
import { Form, Button, message, Radio, Spin, Input, Select, Cascader, Checkbox, Upload, Icon } from 'antd';
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;
const CheckboxGroup = Checkbox.Group;
const { TextArea } = Input;
import FinancingItem from '../FormItem/financingItem.jsx'
import InvestItem from '../FormItem/investItem.jsx'
import DeadlineItem from '../FormItem/deadlineItem.jsx'
import MortgageItem from '../FormItem/mortgageItem.jsx'
const provinceList = {
    "110000": "",
    "120000": "",
    "130000": "",
    "140000": "",
    "150000": "",
    "210000": "",
    "220000": "",
    "230000": "",
    "310000": "",
    "320000": "",
    "330000": "",
    "340000": "",
    "350000": "",
    "360000": "",
    "370000": "",
    "410000": "",
    "420000": "",
    "430000": "",
    "440000": "",
    "450000": "",
    "460000": "",
    "500000": "",
    "510000": "",
    "520000": "",
    "530000": "",
    "540000": "",
    "610000": "",
    "620000": "",
    "630000": "",
    "640000": "",
    "650000": "",
    "710000": "",
    "810000": "",
    "820000": ""
}
export class DebtsForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            disabled: props.actionType == "detail" ? true : false,
            loading: false,
            "assetsType": {
                "debts":[],
                "stock": [],
                "transfer": []
            },
            "industryList": [],
            provinceList,
            "key": "",      //
            "title": "",    //
            "category": "",         //
            "stockType": "",  ///
            "type": [],        //
            "industry": [],    //
            "region": [],     //
            "financingProfits": {
                "isNeed": "0",
                "number": ""
            },     //
            "invest": {     //
                "min": "",
                "max": ""
            },
            "deadline": {   //
                "min": "",
                "max": ""
            },
            "minRate" : "",     //
            "mortgage": {       //
                "isNeed": "0",    //
                "type": [],  //
                "rate": {       //
                    "min": "",
                    "max": ""
                }
            },
            "guarantee": {
                "isNeed": "0",    //
                "method": ""   //
            },
            "estimateBenefits": {
                "times": "",
                "type": ""
            },
            "needCost": "0",        //
            "detail": "",   //
            "accessory": {        //
                "name": "",
                "link": ""
            },
            'fileList': []
        };
        ['handleSubmit', 'resetForm', 'mortgageChange', 'guaranteeChange', 'uploadChange'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentWillMount() {
        let that = this;
        this.setState({loading: true});
        adminAjax({
            url: "/delegateFund/getInfoDetail",
            method: "post",
            data: {id: that.props.infoID ? that.props.infoID : ""},
            callback: function(data) {
                let newData = data;
                that.setState({
                    loading: false,
                    "assetsType": {
                        "debts":newData.debts,
                        "stock": newData.stock,
                        "transfer": newData.assetsAcquisition
                    },
                    "industryList": newData.industryList,
                    "key": newData.key,      //
                    "title": newData.title,    //
                    "category": newData.category,         //
                    "stockType": newData.stockType,  ///
                    "type": newData.investmentType.split(','),        //
                    "industry": newData.investmentIndustry.split(','),    //
                    "region": newData.region,     //
                    "financingProfits": {
                        "isNeed": newData.isNeedLastYearProfits.toString(),
                        "number": newData.lastYearProfitsNum
                    },     //
                    "invest": {     //
                        "min": newData.investMin,
                        "max": newData.investMax
                    },
                    "deadline": {   //
                        "min": newData.deadlineMin,
                        "max": newData.deadlineMax
                    },
                    "minRate" : newData.minRate,     //
                    "mortgage": {       //
                        "isNeed": newData.isNeedMortgage.toString(),    //
                        "type": newData.mortgageType.split(','),  //
                        "rate": {       //
                            "min": newData.mortgageRateMin,
                            "max": newData.mortgageRateMax
                        }
                    },
                    "guarantee": {
                        "isNeed": newData.isNeedGuarantee.toString(),    //
                        "method": newData.guaranteeMethod   //
                    },
                    "estimateBenefits": {
                        "times": newData.estimateBenefitsTimes,
                        "type": newData.estimateBenefitsType
                    },
                    "needCost": newData.needCost.toString(),        //
                    "detail": newData.detail,   //
                    "accessory": {        //
                        "name": newData.accessoryName,
                        "link": newData.accessoryLink
                    },
                    fileList: [{
                        uid: "random",
                        name: newData.accessorName,
                        url: newData.accessoryLink,
                        status: "done",
                        response: 'Server Error 500'
                    }]
                })
            }
        })
    }

    financingProfitsCheck(rule, value, callback) {
        if ((value.isNeed == "0") || (value.isNeed > 0 && value.number)) {
            callback();
            return;
        }
        callback('!');
    }

    investCheck(rule, value, callback) {
        if(!value.min|| !value.max) {
            callback('!');
        } else {
            if(value.max < value.min) {
                callback("!")
            }
            callback();
            return;
        }
    }

    deadlineCheck(rule, value, callback) {
        if(!value.min|| !value.max) {
            callback('!');
        } else {
            if(value.max < value.min) {
                callback("!")
            }
            callback();
            return;
        }
    }

    mortgageChange(e) {
        this.state.mortgage.isNeed = e.target.value;
        this.setState({
            mortgage: this.state.mortgage
        });
    }

    guaranteeChange(e) {
        this.state.guarantee.isNeed = e.target.value;
        this.setState({
            guarantee: this.state.guarantee
        });
    }

    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if(!err) {
                let params = {
                    title: values.title,
                    category: values.category,
                    investmentType: values.type.join(","),
                    investmentIndustry: values.industry.join(","),
                    region: values.region,
                    isNeedLastYearProfits: values.financingProfits.isNeed,
                    lastYearProfitsNum: values.financingProfits.number,
                    investMin: values.invest.min,
                    investMax: values.invest.max,
                    deadlineMin: values.deadline.min,
                    deadlineMax: values.deadline.max,
                    minRate: values.minRate,
                    isNeedMortgage: values.mortgage.isNeed,
                    mortgageType: values.mortgage.isNeed ? values.mortgage.type : "",
                    mortgageRateMin: values.mortgage.isNeed ? values.mortgage.rate.min : 0,
                    mortgageRateMax: values.mortgage.isNeed ? values.mortgage.rate.max : 0,
                    isNeedGuarantee: values.guarantee.isNeed,
                    guaranteeMethod: values.guarantee.method,
                    needCost: values.needCost,
                    detail: values.detail
                }
                adminAjax({
                    url: "/delegateFund/edit",
                    method: "post",
                    data: this.props.actionType == "edit" ? Object.assign(params, {id: this.props.infoID}, {accessoryName: this.state.accessory.name, accessoryLink: this.state.accessory.link}) : Object.assign(params, {accessoryName: this.state.accessory.name, accessoryLink: this.state.accessory.link}),
                    callback: function(data) {
                        message.success("!", 1, function() {
                            hashHistory.push("/delegateFund")
                        });
                    }
                });
            }
        })
    }

    return() {
        history.back();
    }

    resetForm() {
        this.props.form.resetFields();
    }

    uploadChange(info) {
        let that = this;
        let file = info.file;
        let fileList = info.fileList;
        this.setState({loading: true});
        if(fileList.length > 0) {
            if(fileList.length > 1) {
                fileList = [file];
            }
            if(fileList[0].status == "error") {
                fileList = [];
                message.error("!");
            }
            if(fileList[0].status == "done") {
                fileList[0].url = `${fileList[0].response.url}`;
                this.setState({
                    accessory: {
                        name: fileList[0].name,
                        link: fileList[0].url
                    }
                })
            }
        } else {
            this.setState({
                accessory: {
                    name: "",
                    link: ""
                }
            })
        }

        this.setState({fileList, loading: false});
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 3 },
            wrapperCol: { span: 14 }
        };
        var regionOps = [];
        for(var key in this.state.provinceList) {
            regionOps.push({
                key,
                value: this.state.provinceList[key]
            });
        }

        return (
            <Spin spinning={this.state.loading} tip="Loading...">
                <Form>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("title", {
                            rules: [{required: true, message: "!"}],
                            initialValue: this.state.title
                        })(
                            <Input disabled={this.state.disabled} />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                    >
                        {getFieldDecorator("category", {
                            initialValue: "1"
                        })(
                            <Select disabled>
                                <Option value="1"></Option>
                                <Option value="2"></Option>
                                <Option value="3"></Option>
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("type", {
                            rules: [{
                                required: true,
                                message: "!"
                            }],
                            initialValue: this.state.type
                        })(
                            <Select 
                                placeholder=""
                                mode="multiple"
                                disabled={this.state.disabled}>
                                {
                                    this.state.assetsType.debts.map(function(item) {
                                        return <Option value={item.key.toString()} key={item.key}>{item.value}</Option>
                                    })
                                }
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("industry", {
                            rules: [{
                                required: true,
                                message: "!"
                            }],
                            initialValue: this.state.industry
                        })(
                            <Select 
                                placeholder=""
                                mode="multiple"
                                disabled={this.state.disabled}>
                                {
                                    this.state.industryList.map(function(item) {
                                        return <Option value={item.key} key={item.key}>{item.value}</Option>
                                    })
                                }
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("region", {
                            rules: [{
                                required: true,
                                message: "!"
                            }],
                            initialValue: this.state.region
                        })(
                            <Select
                                placeholder=""
                                mode="multiple"
                                disabled={this.state.disabled}
                            >
                                {regionOps.map(item =>
                                    <Option key={item.key} value={item.key.toString()}>{item.value}</Option>
                                )}
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("financingProfits", {
                            rules: [{
                                validator: this.financingProfitsCheck
                            }],
                            initialValue: {
                                isNeed: this.state.financingProfits.isNeed,
                                number: this.state.financingProfits.number }
                        })(<FinancingItem disabled={this.state.disabled} />)}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("invest", {
                            rules: [{
                                validator: this.investCheck
                            }],
                            initialValue: {
                                min: this.state.invest.min,
                                max: this.state.invest.max
                            }
                        })(<InvestItem disabled={this.state.disabled} />)}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("deadline", {
                            rules: [{
                                validator: this.deadlineCheck
                            }],
                            initialValue: {
                                min: this.state.deadline.min,
                                max: this.state.deadline.max
                            }
                        })(<DeadlineItem disabled={this.state.disabled} />)}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("minRate", {
                            rules: [{
                                required: true,
                                message: "!"
                            }],
                            initialValue: this.state.minRate
                        })(
                            <Input disabled={this.state.disabled} />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("mortgage.isNeed", {
                            rules: [{
                                required: true,
                                message: "!"
                            }],
                            initialValue: this.state.mortgage.isNeed
                        })(
                            <RadioGroup onChange={this.mortgageChange} disabled={this.state.disabled}>
                                <Radio value="1"></Radio>
                                <Radio value="0"></Radio>
                            </RadioGroup>
                        )}
                    </FormItem>
                    {
                        this.state.mortgage.isNeed == "1" ?
                        <FormItem
                            {...formItemLayout}
                            label="/"
                            hasFeedback={!this.state.disabled}
                        >
                            {getFieldDecorator("mortgage.type", {
                                initialValue: this.state.mortgage.type
                            })(
                                <CheckboxGroup disabled={this.state.disabled}>
                                    <Checkbox value="1"></Checkbox>
                                    <Checkbox value="2"></Checkbox>
                                    <Checkbox value="3"></Checkbox>
                                </CheckboxGroup>
                            )}
                        </FormItem> : ""
                    }
                    {
                        this.state.mortgage.isNeed == "1" ?
                        <FormItem
                            {...formItemLayout}
                            label="/"
                        >
                            {getFieldDecorator("mortgage.rate", {
                                initialValue: {
                                    min: this.state.mortgage.rate.min,
                                    max: this.state.mortgage.rate.max
                                }
                            })(<MortgageItem disabled={this.state.disabled} />)}
                        </FormItem> : ""
                    }
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("guarantee.isNeed", {
                            rules: [{
                                required: true,
                                message: "!"
                            }],
                            initialValue: this.state.guarantee.isNeed
                        })(
                            <RadioGroup onChange={this.guaranteeChange} disabled={this.state.disabled}>
                                <Radio value="1"></Radio>
                                <Radio value="0"></Radio>
                            </RadioGroup>
                        )}
                    </FormItem>
                    {
                        this.state.guarantee.isNeed == "1" ?
                        <FormItem
                                {...formItemLayout}
                                label=""
                            >
                                {getFieldDecorator("guarantee.method", {
                                    initialValue: this.state.guarantee.method
                                })(
                                    <Input disabled={this.state.disabled} />
                                )}
                        </FormItem> : ""
                    }
                    <FormItem
                        {...formItemLayout}
                        label=""
                    >
                        {getFieldDecorator("needCost", {
                            initialValue: this.state.needCost
                        })(
                            <RadioGroup disabled={this.state.disabled} >
                                <Radio value="1">(/)</Radio>
                                <Radio value="0"></Radio>
                            </RadioGroup>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("detail", {
                            rules: [{required: true, message: "!"}],
                            initialValue: this.state.detail
                        })(
                            <TextArea disabled={this.state.disabled} autosize={{ minRows: 3 }} />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label=""
                    >
                        <Upload
                            name="file"
                            action="/admin/upload"
                            fileList={this.state.fileList}
                            onChange={this.uploadChange}
                        >
                            <Button>
                                <Icon type="upload" />
                            </Button>
                        </Upload>
                    </FormItem>
                    <FormItem wrapperCol={{ span: 8, offset: 3 }}>
                        {
                            !this.state.disabled ? <Button type="primary" onClick={this.handleSubmit} style={{"marginRight": "20px"}}></Button> : ""
                        }
                        {
                            !this.state.disabled ? <Button type="danger" onClick={this.resetForm} style={{"marginRight": "20px"}}></Button> : ""
                        }
                        <Button icon="rollback" onClick={this.return}></Button>
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedDebtsForm = Form.create({})(DebtsForm);

module.exports = WrappedDebtsForm;


